import { Component, OnInit } from '@angular/core';
import { Room1, RoomStatus, RoomType } from '../entity';
import {UserService} from '../user.service';
import {UniversityService} from '../university.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-add-room',
  templateUrl: './add-room.component.html',
  styleUrls: ['./add-room.component.css']
})
export class AddRoomComponent implements OnInit {
  roomStatuses: RoomStatus[];
  roomTypes: RoomType[];
  roomStatus: RoomStatus;
  roomType: RoomType;
  room: Room1;

  constructor(private roomService: UniversityService,
              private userService: UserService,
              private router: Router) {}

  ngOnInit() {
    this.getRoomStatuses();
    this.getRoomTypes();
  }
  getRoomStatuses() {
    this.roomService.getRoomStatuses().subscribe( res => this.roomStatuses = res);
  }
  getRoomStatus(id: number) {
    this.roomService.getRoomStatus(id).subscribe( res => this.roomStatus = res);
  }
  getRoomTypes() {
    this.roomService.getRoomTypes().subscribe(res => this.roomTypes = res);
  }
  getRoomType(id: number) {
    this.roomService.getRoomType(id).subscribe( res => this.roomType = res);
  }

  goBack() {
    this.router.navigateByUrl('index/room');
  }
  save(no: string): void {
    this.room = {no: no.trim(),
      roomStatus: this.roomStatus, roomType: this.roomType};
    if (!no) { return; }
    this.roomService.addRoom(this.room).subscribe(
      (res) => {
        alert(res.message);
        this.goBack();
      }
    );
  }

}
