import { Component, OnInit } from '@angular/core';
import {Booking} from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-booking',
  templateUrl: './booking.component.html',
  styleUrls: ['./booking.component.css']
})
export class BookingComponent implements OnInit {
  bookings: Booking[];
  constructor(private bookingService: UniversityService) { }

  ngOnInit() {
    this.getBookings();
  }
  getBookings(): void {
    this.bookingService.getBookings()
      .subscribe(res => this.bookings = res);
  }
  delete(booking: Booking | number): void {
    this.bookings = this.bookings.filter(h => h !== booking);
    this.bookingService.deleteBooking(booking).subscribe(res => alert(res.message));
  }
}
