import { Component, OnInit } from '@angular/core';
import {Booking, Client} from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-client',
  templateUrl: './client.component.html',
  styleUrls: ['./client.component.css']
})
export class ClientComponent implements OnInit {
  clients: Client[];
  client: Client;
  constructor(private clientService: UniversityService) { }

  ngOnInit() {
    this.getClients();
  }
  getClients(): void {
    this.clientService.getClients()
      .subscribe(res => this.clients = res);
  }
  delete(client: Client | number): void {
    this.clients = this.clients.filter(h => h !== client);
    this.clientService.deleteClient(client).subscribe(res => alert(res.message));
  }
}
