import { Component, OnInit } from '@angular/core';
import {CheckIn, Room, Room1, RoomStatus, RoomType} from '../entity';
import {UniversityService} from '../university.service';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-graduate-project',
  templateUrl: './room.component.html',
  styleUrls: ['./room.component.css']
})
export class RoomComponent implements OnInit {
  private rooms: Room[];
  room: Room;
  constructor(private router: Router, private route: ActivatedRoute, private roomService: UniversityService) { }

  ngOnInit() {
    this.getRooms();

  }

  getRooms(): void {
    this.roomService.getRooms().subscribe(
      res => { this.rooms = res;
      }
    );
  }

  delete(room: Room | number): void {
    this.rooms = this.rooms.filter(h => h !== room);
    this.roomService.deleteRoom(room).subscribe( res => alert(res.message));
  }
  save(room): void {
    if (room.roomStatus.id === 1) {
      this.router.navigateByUrl('/index/addInformation');
    } else if (room.roomStatus.id === 2) {
      alert('房间已入住，不可重复入住');
    }
  }


}

