import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import { Location } from '@angular/common';
import {Information,  Room, RoomType, RoomPrice} from '../entity';

@Component({
  selector: 'app-update-graduate-project',
  templateUrl: './update-information.component.html',
  styleUrls: ['./update-information.component.css']
})
export class UpdateInformationComponent implements OnInit {
  information: Information;
  rooms: Room[];
  constructor(
    private route: ActivatedRoute,
    private informationService: UniversityService,
    private location: Location
  ) { }

  ngOnInit() {
    const id = this.route.snapshot.params.id; // 要修改专业的id
    this.informationService.getInformation(id).subscribe(res => {this.information = res; } );
    this.getRooms();
  }
  getRooms(): void {
    this.informationService.getRooms ()
      .subscribe(res => this.rooms = res);
  }

  save(): void { // 多个文本框数据
    this.informationService.updateInformation(this.information)
      .subscribe((res) => {
        alert(res.message);
        this.goBack();
      });
  }

  goBack(): void {
    this.location.back();
  }
  compareFn(o1: Information, o2: Information): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }


}
