import { Component, OnInit } from '@angular/core';
import {Booking, RoomType} from '../entity';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-update-booking',
  templateUrl: './update-booking.component.html',
  styleUrls: ['./update-booking.component.css']
})
export class UpdateBookingComponent implements OnInit {
  booking: Booking;
  roomTypes: RoomType[];
  constructor(private location: Location,
              private route: ActivatedRoute,
              private bookingService: UniversityService) { }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.bookingService.getBooking(id).subscribe(res => this.booking = res);
    this.bookingService.getRoomTypes().subscribe(res => this.roomTypes = res);
  }
  goBack(): void {
    this.location.back();
  }
  save(): void {
    this.bookingService.updateBooking(this.booking)
      .subscribe((res) => {alert(res.message); this.goBack(); });
  }
  compareFn(o1: Booking, o2: Booking): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

}
