import { Component, OnInit } from '@angular/core';
import {CheckIn} from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-checkin',
  templateUrl: './checkin.component.html',
  styleUrls: ['./checkin.component.css']
})
export class CheckinComponent implements OnInit {

  checkIns: CheckIn[];
  constructor(private checkinService: UniversityService) { }

  ngOnInit() {
    this.getCheckins();
  }
  getCheckins(): void {
    this.checkinService.getCheckIns ()
      .subscribe(res => this.checkIns = res);
  }
  delete(checkin: CheckIn | number): void {
    this.checkIns = this.checkIns.filter(h => h !== checkin);
    this.checkinService.deleteCheckIn(checkin).subscribe(res => alert(res.message));
  }
}
