import { Component, OnInit } from '@angular/core';
import { Information} from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-graduate-project',
  templateUrl: './information.component.html',
  styleUrls: ['./information.component.css']
})
export class InformationComponent implements OnInit {

  informations: Information[];
  constructor(private informationService: UniversityService) { }

  ngOnInit() {
    this.getInformations();
  }
  getInformations(): void {
    this.informationService.getInformations ()
      .subscribe(res => this.informations = res);
  }
  delete(information: Information | number): void {
    this.informations = this.informations.filter(h => h !== information);
    this.informationService.deleteInformation(information).subscribe(res => alert(res.message));
  }
}

