import {Component, Input, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {UniversityService} from '../university.service';
import {Booking1, RoomType} from '../entity';
import {Location} from '@angular/common';

@Component({
  selector: 'app-add-booking',
  templateUrl: './add-booking.component.html',
  styleUrls: ['./add-booking.component.css']
})
export class AddBookingComponent implements OnInit {
  @Input() booking: Booking1;
  constructor(private router: Router, private location: Location,
              private bookingService: UniversityService) { }
   roomType: RoomType;
  roomTypes: RoomType[];
  ngOnInit() {
    this.getRoomTypes();
  }
  getRoomTypes() {
    this.bookingService.getRoomTypes().subscribe( res => this.roomTypes = res);
  }
  save(name: string, phoneNumber: string, idCard: string,
       inTime: string, outTime: string): void { // 多个文本框数据
    this.booking = {name: name.trim(), idCard: idCard.trim(), phoneNumber: phoneNumber.trim(),
       inTime: inTime.trim(), outTime: outTime.trim(), roomType: this.roomType
    };
    if (!name) { return; }
    this.bookingService.addBooking(this.booking)
      .subscribe(() => this.goBack());
  }
  goBack(): void {
    this.location.back();
  }
}
