import { Component, OnInit } from '@angular/core';
import {Waiter} from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-waiter',
  templateUrl: './waiter.component.html',
  styleUrls: ['./waiter.component.css']
})
export class WaiterComponent implements OnInit {
  waiters: Waiter[];
  constructor(private waiterService: UniversityService) { }

  ngOnInit() {
    this.getWaiters();
  }
  getWaiters(): void {
    this.waiterService.getWaiters()
      .subscribe(res => this.waiters = res);
  }
  delete(waiter: Waiter | number): void {
    this.waiters = this.waiters.filter(h => h !== waiter);
    this.waiterService.deleteWaiter(waiter).subscribe(res => alert(res.message));
  }
}
