import {Component, Input, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {UniversityService} from '../university.service';
import {Waiter1, RoomType} from '../entity';

@Component({
  selector: 'app-add-waiter',
  templateUrl: './add-waiter.component.html'
})
export class AddWaiterComponent implements OnInit {
  @Input() waiter: Waiter1;
  constructor(private router: Router,
              private waiterService: UniversityService) { }
   roomType: RoomType;
  roomTypes: RoomType[];
  ngOnInit() {
    this.getRoomTypes();
  }
  getRoomTypes() {
    this.waiterService.getRoomTypes().subscribe( res => this.roomTypes = res);
  }
  save(no: string, name: string, workdays: string, salary: string): void { // 多个文本框数据
    this.waiter = { no: no.trim(), name: name.trim(), workdays: workdays.trim(), salary: salary.trim()
    };
    if (!name) { return; }
    this.waiterService.addWaiter(this.waiter)
      .subscribe(() => this.goBack());
  }
  goBack(): void {
    this.router.navigateByUrl('managerIndex/waiter');
  }
}
