import { Component, OnInit } from '@angular/core';
import {Waiter, RoomType} from '../entity';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-update-waiter',
  templateUrl: './update-waiter.component.html'

})
export class UpdateWaiterComponent implements OnInit {
  waiter: Waiter;
  roomTypes: RoomType[];
  constructor(private location: Location,
              private route: ActivatedRoute,
              private waiterService: UniversityService) { }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.waiterService.getWaiter(id).subscribe(res => this.waiter = res);
    this.waiterService.getRoomTypes().subscribe(res => this.roomTypes = res);
  }
  goBack(): void {
    this.location.back();
  }
  save(): void {
    this.waiterService.updateWaiter(this.waiter)
      .subscribe((res) => {alert(res.message); this.goBack(); });
  }
  compareFn(o1: Waiter, o2: Waiter): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

}
