import {Component, Input, OnInit} from '@angular/core';
import {
  Booking,
  Information, Information1,
  Room,
  RoomPrice, RoomStatus,
  RoomType,
} from '../entity';
import { Location } from '@angular/common';
import {UniversityService} from '../university.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-add-graduate-project',
  templateUrl: './add-information.component.html',
  styleUrls: ['./add-information.component.css']
})
export class AddInformationComponent implements OnInit {
  rooms: Room[];
  information: Information1;
  room: Room;
  constructor(
    private route: ActivatedRoute,
    private informationService: UniversityService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getRooms();
  }

  getRooms(): void {
    this.informationService.getRooms()
      .subscribe(res => this.rooms = res);
  }

  save(name: string, idCard: string,  phoneNumber: string, inTime: string, outTime: string): void {
    this.information = { name: name.trim(), idCard: idCard.trim(), phoneNumber: phoneNumber.trim(),  room: this.room,
      inTime: inTime.trim(), outTime: outTime.trim()};
    if (!idCard) { return; }
    // @ts-ignore
    this.informationService.addInformation(this.information)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }

}
