import {Component, Input, OnInit} from '@angular/core';
import {Room, RoomStatus, RoomType} from '../entity';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import { Location } from '@angular/common';


@Component({
  selector: 'app-update-room',
  templateUrl: './update-room.component.html',
  styleUrls: ['./update-room.component.css']
})
export class UpdateRoomComponent implements OnInit {
  room: Room;
  roomStatuses: RoomStatus[];
  roomTypes: RoomType[];
  constructor(private route: ActivatedRoute, private roomService: UniversityService,  private location: Location) { }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.roomService.getRoom(id).subscribe( res =>  this.room = res );
    this.roomService.getRoomStatuses().subscribe( res => this.roomStatuses = res);
    this.roomService.getRoomTypes().subscribe( res => this.roomTypes = res);
  }
  goBack(): void {
    this.location.back();
  }
  save() {
    this.roomService.updateRoom(this.room).subscribe(
      (res) => {
        alert(res.message);
        this.goBack();
      }
    );
  }
  compareFn(o1: Room, o2: Room): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
}
