import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import { UniversityService } from '../university.service';
import {UserService} from '../user.service';
import {Actor, User} from '../entity';

// declare var jQuery: any;
@Component({
  selector: 'app-login',
  templateUrl: './Login.component.html',
  styleUrls: ['./Login.component.css']
})
export class LoginComponent implements OnInit {
  private user: User;
  private uname: string;
  private password: string;
  userName: string;
  actor: Actor;

  constructor(private router: Router, private loginService: UniversityService, private userService: UserService) { }

  ngOnInit() { // 组件被创建
  }
  login(un: string, up: string) { // 登录跳转页面
    this.uname = un;
    this.password = up;
    this.loginService.checkUser({username: this.uname, password: this.password})
      .subscribe((res) => {
        if (res.username) {
          window.localStorage.setItem('userName', res.actor.name);
          this.actor = { id: res.actor.id, no: res.actor.no, name: res.actor.name };
          this.userService.setActor(this.actor);
          this.loginService.checkRole(res.id).subscribe((response) => {
            if (response.role.id === 1) {
              this.router.navigateByUrl('managerIndex/home');
            } else if (response.role.id === 2) {
              this.router.navigateByUrl('index/home');
            } else if (response.role.id === 3) {
              this.router.navigateByUrl('browse');
            }
          });
        } else {
          alert(res.message);
        }
      });
  }
}
