import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import {ClientIndexComponent} from './clientIndex/clientIndex.component';
import { IndexComponent } from './index/index.component';
import {LoginComponent} from './login/login.component';
import {LogoutComponent} from './login/logout.component';
import {HomeComponent} from './home/home.component';
import {ManagerIndexComponent} from './managerIndex/managerIndex.component';
import {RoomComponent} from './room/room.component';
import {AddRoomComponent} from './room/add-room.component';
import {UpdateRoomComponent} from './room/update-room.component';
import {InformationComponent} from './Information/information.component';
import {UpdateInformationComponent} from './Information/update-information.component';
import {AddInformationComponent} from './Information/add-information.component';
import {ClientComponent} from './client/client.component';
import {UpdateClientComponent} from './client/update-client.component';
import {CheckinComponent} from './checkin/checkin.component';
import {BookingComponent} from './booking/booking.component';
import {AddBookingComponent} from './booking/add-booking.component';
import {UpdateBookingComponent} from './booking/update-booking.component';
import {UpdateWaiterComponent} from './waiter/update-waiter.component';
import {WaiterComponent} from './waiter/waiter.component';
import {AddWaiterComponent} from './waiter/add-waiter.component';
import {HighchartsComponent} from './highcharts/highcharts.component';
import {MyCkeditorComponent} from './home/ckeditor.component';
import {BrowseComponent} from './browse/browse.component';
import {DatailComponent} from './datail/datail.component';

const routes: Routes = [
  {path: '', component: LoginComponent, pathMatch: 'full'},
  {path: 'logout', component: LogoutComponent},
  {path: 'browse', component: BrowseComponent},
  {path: 'datail', component: DatailComponent},
  {path: 'managerIndex', component: ManagerIndexComponent, children: [
      {path: 'information', component: InformationComponent},
      {path: 'addInformation', component: AddInformationComponent},
      {path: 'updateInformation/:id', component: UpdateInformationComponent},
      {path: 'highcharts', component: HighchartsComponent},
      {path: 'client', component: ClientComponent},
      {path: 'updateClient/:id', component: UpdateClientComponent},
      {path: 'waiter', component: WaiterComponent},
      {path: 'addWaiter', component: AddWaiterComponent},
      {path: 'updateWaiter/:id', component: UpdateWaiterComponent},
      {path: 'home', component: HomeComponent},
      {path: 'home', component: MyCkeditorComponent},
      ]},
  {path: 'index', component: IndexComponent, children: [
      {path: 'room', component: RoomComponent},
      {path: 'addRoom', component: AddRoomComponent},
      {path: 'updateRoom/:id', component: UpdateRoomComponent},
      {path: 'addInformation', component: AddInformationComponent},
      {path: 'booking', component: BookingComponent},
      {path: 'addBooking', component: AddBookingComponent},
      {path: 'updateBooking/:id', component: UpdateBookingComponent},
      {path: 'checkIn', component: CheckinComponent},
      {path: 'home', component: HomeComponent},
      {path: 'home', component: MyCkeditorComponent},
    ]},
  {path: 'clientIndex', component: ClientIndexComponent, children: [
      {path: 'addBooking', component: AddBookingComponent},

      {path: 'home', component: MyCkeditorComponent},
    ]}
];
@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
